import {HttpRequest} from '@angular/common/http';

import {TestRequest} from './request';

/**
 * Defines a matcher for requests based on URL, method, or both.
 *
 * @experimental
 */
export interface RequestMatch {
  method?: string;
  url?: string;
}
/**
 * Controller to be injected into tests, that allows for mocking and flushing
 * of requests.
 * 
 * \@experimental
 * @abstract
 */
export abstract class HttpTestingController {
/**
 * Search for requests that match the given parameter, without any expectations.
 * @abstract
 * @param {?} match
 * @return {?}
 */
match(match: string|RequestMatch|((req: HttpRequest<any>) => boolean)) {}
/**
 * Expect that a single request has been made which matches the given URL, and return its
 * mock.
 * 
 * If no such request has been made, or more than one such request has been made, fail with an
 * error message including the given request description, if any.
 * @abstract
 * @param {?} url
 * @param {?=} description
 * @return {?}
 */
expectOne(url: string, description?: string) {}
/**
 * Expect that a single request has been made which matches the given parameters, and return
 * its mock.
 * 
 * If no such request has been made, or more than one such request has been made, fail with an
 * error message including the given request description, if any.
 * @abstract
 * @param {?} params
 * @param {?=} description
 * @return {?}
 */
expectOne(params: RequestMatch, description?: string) {}
/**
 * Expect that a single request has been made which matches the given predicate function, and
 * return its mock.
 * 
 * If no such request has been made, or more than one such request has been made, fail with an
 * error message including the given request description, if any.
 * @abstract
 * @param {?} matchFn
 * @param {?=} description
 * @return {?}
 */
expectOne(matchFn: ((req: HttpRequest<any>) => boolean), description?: string) {}
/**
 * Expect that a single request has been made which matches the given condition, and return
 * its mock.
 * 
 * If no such request has been made, or more than one such request has been made, fail with an
 * error message including the given request description, if any.
 * @abstract
 * @param {?} match
 * @param {?=} description
 * @return {?}
 */
expectOne(
      match: string|RequestMatch|((req: HttpRequest<any>) => boolean),
      description?: string) {}
/**
 * Expect that no requests have been made which match the given URL.
 * 
 * If a matching request has been made, fail with an error message including the given request
 * description, if any.
 * @abstract
 * @param {?} url
 * @param {?=} description
 * @return {?}
 */
expectNone(url: string, description?: string) {}
/**
 * Expect that no requests have been made which match the given parameters.
 * 
 * If a matching request has been made, fail with an error message including the given request
 * description, if any.
 * @abstract
 * @param {?} params
 * @param {?=} description
 * @return {?}
 */
expectNone(params: RequestMatch, description?: string) {}
/**
 * Expect that no requests have been made which match the given predicate function.
 * 
 * If a matching request has been made, fail with an error message including the given request
 * description, if any.
 * @abstract
 * @param {?} matchFn
 * @param {?=} description
 * @return {?}
 */
expectNone(matchFn: ((req: HttpRequest<any>) => boolean), description?: string) {}
/**
 * Expect that no requests have been made which match the given condition.
 * 
 * If a matching request has been made, fail with an error message including the given request
 * description, if any.
 * @abstract
 * @param {?} match
 * @param {?=} description
 * @return {?}
 */
expectNone(
      match: string|RequestMatch|((req: HttpRequest<any>) => boolean), description?: string) {}
/**
 * Verify that no unmatched requests are outstanding.
 * 
 * If any requests are outstanding, fail with an error message indicating which requests were not
 * handled.
 * 
 * If `ignoreCancelled` is not set (the default), `verify()` will also fail if cancelled requests
 * were not explicitly matched.
 * @abstract
 * @param {?=} opts
 * @return {?}
 */
verify(opts?: {ignoreCancelled?: boolean}) {}
}
